import back from '../../components/back.vue';
import uniCalendar from "../../components/uni-calendar/uni-calendar"
export default {
	data() {
		return {
			pageNameFlag:1,
			pageName:'数据完整性详情',
			startDate:'',
			endDate:''
		}
	},
	methods: {
		previewPic(url) {
			uni.previewImage({
				urls: [url],
				current:0,
				longPressActions: {
					itemList: ['保存图片'],
					success: function(data) {
						if (data.tapIndex == 0) {
							uni.showLoading({
								title: "图片保存中"
							});
							uni.downloadFile({
								url: url,
								success(result) {
									var tempFilePath = result.tempFilePath;
									uni.saveImageToPhotosAlbum({
										filePath: tempFilePath,
										success() {
											uni.showToast({
												icon: "success",
												title: "保存成功",
												duration: 2000
											})
										}
									})
								},
								complete() {
									uni.hideLoading();
								}
							})
						}
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
		chooseDate(){
		    this.$refs.calendar.open();
		},
		confirm(e){
			var that = this;
			var before = e.range.before;
			var after = e.range.after;
			var b = parseInt(before.replace('-','').replace('-',''));
			var a = parseInt(after.replace('-','').replace('-',''));
			if(a>b){
				that.startDate = before;
				that.endDate = after;
			}else{
				that.startDate = after;
				that.endDate = before;
			}
		}
	},
	onLoad() {
		var that=this;
		that.endDate=that.formatTime(new Date());
		that.startDate=that.endDate.substring(0,8)+'01';
	},
	components:{
		back,
		uniCalendar
	}
}
